import React from "react";
import { Field, reduxForm } from "redux-form";
import "./post-form.sass";
import {
  required,
  maxLengthCreator,
  minLengthCreator,
} from "./../../untils/validators/validators";
import { Element } from "../../common/FormsControls/FormsControls";
import Button from '@material-ui/core/Button';

const maxLength30 = maxLengthCreator(30);
const maxLength1000 = maxLengthCreator(1000);
const minLength = minLengthCreator(1);

const input = Element("input")

const PostForm = (props) => {
  let { handleSubmit } = props;
  return (
    <form onSubmit={handleSubmit} className="post-form">
      <Button className="post-form_button" type="submit" variant="outlined" color="primary">Add Post</Button>
      <Field
        validate={[required, maxLength30, minLength]}
        placeholder="Input title of story"
        name={"postMessageContent"}
        component={input}
        className="post-form_input-title"
        autoComplete="off"
      />
      <Field
        validate={[required, maxLength1000, minLength]}
        placeholder="Input your story"
        name={"postMessageTitle"}
        component={input}
        className="post-form_input-content"
        autoComplete="off"
      />
    </form>
  );
};

const PostReduxForm = reduxForm({
  form: "post",
})(PostForm);
export default PostReduxForm;
