import React from "react";
import style from "./description.module.css";
import Preloader from "../../../../common/preloader/Preloader";
import StatusHooks from "./status/statusHooks";
import { Typography } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  nameUser: {
    fontSize: "20px",
    fontWeight: "800"
  }
});

let Description = ({ profile, status, updateUserStatus }) => {
  const classes = useStyles();
  if (profile) {
    if (profile.aboutMe) {
      var description = profile.aboutMe;
    } else description = "Here aren`t description";
  } else {
    return <Preloader />;
  }

  return (
    <div className={style.description}>
      <Typography variant="h2" className={classes.nameUser}>{profile.fullName}</Typography>
      <h3 className={style.title}>Discription</h3>
      <div className={style.content}>{description}</div>

      <div className={style.status}>
        <StatusHooks status={status} updateUserStatus={updateUserStatus} />
      </div>
    </div>
  );
};

export default Description;
