import React from "react";
import Post from "./post/post";
import Title from "./title/title";
import style from "./posts.module.css";
import PostReduxForm from "../../form/post-form/post-form";

const Posts = ({ postsData, addPost, deletePost }) => {

  let postsItem = postsData.map(post => {
    return <Post id={post.id} title={post.title} key={post.id} message={post.message} deletePost={deletePost}/>;
  });

  let onSubmit = (formData) => {
    addPost(formData);
  };

  return (
    <div className={style.info}>
      <Title />
      <PostReduxForm onSubmit={onSubmit} />
      <div className={style.list}>{postsItem}</div>
    </div>
  );
};

export default Posts;
